.title {
    font-size: 3em;
}

#headerContainer {
    border-bottom: 2px solid black;
}

:root {
    --image-width: 280px;
}

@media screen and (min-width : 360px) {
    :root {
        --image-width: 320px;
    }
}

@media screen and (min-width : 375px) {
    :root {
        --image-width: 335px;
    }
}

@media screen and (min-width : 411px) {
    :root {
        --image-width: 371px;
    }
}

@media screen and (min-width : 414px) {
    :root {
        --image-width: 374px;
    }
}

@media screen and (min-width : 768px) {
    :root {
        --image-width: 728px;
    }
}

@media screen and (min-width : 1024px) {
    :root {
        --image-width: fit-content;
    }
}

.comic {
    width: 280px;
    width: var(--image-width);
}

#comicContainer {
    padding-top: 1.5em;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

#imageContainer {
    margin-left: auto;
    margin-right: auto;
}

#comicName {
    padding-top: 0.5em;
    padding-bottom: 1.5em;
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
    width: -moz-fit-content;
    width: -webkit-fit-content;
}

@media screen and (min-width : 768px) {
    #comicName {
        font: 1.5em "Century Gothic", Futura, sans-serif;
    }
}
@media screen and (min-width : 768px) {
    .buttongroup button {
        font: 1.5em "Century Gothic", Futura, sans-serif;
    }
}

.buttongroup {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.buttongroup button {
    padding-top: 1.5em;
    background: white;
    font: 1em "Century Gothic", Futura, sans-serif;
    border: 2px solid black;
    float: left;
    margin-right: 5px;
    margin-bottom: 1.5em;
    color: black
}

.clearer { clear: both; }

body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

